/**
 * * Media queries logic
 * * @param {string} $breakpoint - notmobile, mobile, tablet, desktop, huge
 * */
body {
  font-size: 16px;
  font-family: "Roboto", Arial, sans-serif;
  color: #444;
  background: #000;
  margin: 0;
  padding: 0;
  background-image: url("../app/img/gradient_bg.png");
  background-position: center bottom;
  background-size: contain;
  background-repeat: no-repeat;
}

* {
  box-sizing: border-box;
}

.grey-text {
  color: #8c8d8e;
}

.underline {
  text-decoration: underline;
}

h1 {
  line-height: 1.2;
  width: 100%;
  margin: 0 0 10px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  h1 {
    font-size: 25px;
  }
}
@media only screen and (min-width: 48em) and (max-width: 62em) {
  h1 {
    font-size: 30px;
  }
}
@media only screen and (min-width: 62em) and (max-width: 90em) {
  h1 {
    font-size: 40px;
  }
}

h2 {
  font-size: 34px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  h2 {
    font-size: 20px;
  }
}
@media only screen and (min-width: 48em) and (max-width: 62em) {
  h2 {
    font-size: 25px;
  }
}
@media only screen and (min-width: 62em) and (max-width: 90em) {
  h2 {
    font-size: 30px;
  }
}

h3 {
  font-size: 24px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  h3 {
    font-size: 15px;
  }
}
@media only screen and (min-width: 48em) and (max-width: 62em) {
  h3 {
    font-size: 20px;
  }
}
@media only screen and (min-width: 62em) and (max-width: 90em) {
  h3 {
    font-size: 25px;
  }
}

h4 {
  font-size: 18px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  h4 {
    font-size: 10px;
  }
}
@media only screen and (min-width: 48em) and (max-width: 62em) {
  h4 {
    font-size: 15px;
  }
}
@media only screen and (min-width: 62em) and (max-width: 90em) {
  h4 {
    font-size: 20px;
  }
}

p, ol, ul {
  font-family: "Roboto", Arial, sans-serif;
  color: #fff;
  font-size: 16px;
  line-height: 1.5;
}

@media only screen and (min-width: 20em) and (max-width: 48em) {
  p {
    font-size: 14px;
  }
}
@media only screen and (min-width: 48em) and (max-width: 62em) {
  p {
    font-size: 16px;
  }
}
@media only screen and (min-width: 62em) and (max-width: 90em) {
  p {
    font-size: 18px;
  }
}

header {
  padding-top: 100px;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  text-align: center;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  header {
    padding-top: 50px;
    grid-template-columns: 1fr;
  }
}
header h1 {
  margin-top: 30px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 200;
}
header h2 {
  color: #1dffff;
  text-transform: uppercase;
  text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.9);
  font-weight: 200;
}
header p {
  color: #d2d5da;
}
header p span {
  color: #fff;
}
header article {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  header article {
    padding: 20px;
  }
}
header article a {
  color: #8c8d8e;
  text-decoration: none;
  font-weight: 200;
}
header article:nth-child(1) {
  grid-area: 1/1/2/2;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  header article:nth-child(1) {
    grid-area: 1/1/2/2;
  }
}
header article:nth-child(2) {
  grid-area: 1/2/2/3;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  header article:nth-child(2) {
    grid-area: 2/1/3/2;
  }
}
header article:nth-child(3) {
  grid-area: 1/3/2/4;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  header article:nth-child(3) {
    grid-area: 3/1/4/2;
  }
}

.banner_img {
  width: 100%;
  background-image: url("../app/img/banner_bg.png");
  background-size: contain;
  background-position: center bottom;
  background-repeat: no-repeat;
  height: 600px;
  min-height: 300px;
  margin-top: -100px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .banner_img {
    height: 300px;
    margin-top: 0px;
  }
}

button {
  background-color: #1dffff;
  color: #000;
  border-color: #1dffff;
  border-radius: 5px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  padding: 15px 35px;
  transition: all 0.3s ease;
  cursor: pointer;
  transition: all 0.3s ease;
}
button:hover {
  background-color: #d49c00;
  border-color: #d49c00;
}

.btn_main {
  background-color: #d49c00;
  border-color: #d49c00;
}
.btn_main:hover {
  background-color: #1dffff;
  border-color: #1dffff;
}

.product-detalis-btn {
  border-color: #00a6ff;
  background-color: #00a6ff;
  color: #fff;
  padding: 10px 20px;
}

.btn_back {
  background-color: #00aaad;
  color: #fff;
  border-color: #1dffff;
  border-radius: 5px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  padding: 10px 20px;
  transition: all 0.3s ease;
  margin-top: 20px;
  cursor: pointer;
  position: relative;
  border: 1px solid #d2d5da;
}
.btn_back::before {
  content: "";
  width: 25px;
  height: 26px;
  background-color: #00aaad;
  position: absolute;
  left: -11px;
  rotate: 45deg;
  top: 4px;
  border-left: 1px solid #d2d5da;
  border-bottom: 1px solid #d2d5da;
  transition: all 0.3s ease;
}
.btn_back:hover {
  background-color: #d49c00;
  border-color: #d49c00;
}
.btn_back:hover::before {
  background-color: #d49c00;
  border-left: 1px solid #d49c00;
  border-bottom: 1px solid #d49c00;
}

section {
  padding: 50px 20px;
  max-width: 1240px;
  margin: 0 auto;
}
section .header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  place-items: center;
  text-align: center;
  text-transform: uppercase;
  margin: 50px 20px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  section .header {
    padding-top: 50px;
    grid-template-columns: 1fr;
  }
}
section .header h2 {
  text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.9);
  color: #1dffff;
  font-weight: 200;
}
section .header h3 {
  color: #1dffff;
  font-weight: 200;
}

.campaign-services {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: stretch;
  padding: 30px 0;
  text-align: center;
  margin: 0 20px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .campaign-services {
    flex-flow: column;
    margin: 20px;
  }
}
.campaign-services .campaign-service-box {
  padding: 30px 0;
  margin: 20px 0;
  display: flex;
  flex: 1;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.campaign-services .campaign-service-box h3 {
  color: #fff;
  margin: 0;
}
.campaign-services .campaign-service-box .campaign-service-box-price {
  font-size: 24px;
  font-weight: 700;
  margin: 0 0 20px;
  color: #fff;
}
.campaign-services .campaign-service-box del {
  color: #8c8d8e;
}
.campaign-services .campaign-service-box button {
  background-color: #00a6ff;
  border-color: #00a6ff;
  width: 150px;
}
.campaign-services .campaign-service-box button:hover {
  background-color: #d49c00;
  border-color: #d49c00;
}
.campaign-services .campaign-service-box a {
  margin: 20px 0;
  color: #0057ba;
  text-decoration: none;
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
}
.campaign-services .campaign-service-box a:hover {
  color: #fff;
  text-decoration: none;
}
.campaign-services .campaign-service-box.featured {
  flex: 1.2;
  margin: -20px -30px;
  color: #fff;
  border-radius: 10px;
  border: 1px solid #d2d5da;
  z-index: 100;
}
.campaign-services .campaign-service-box.featured a {
  color: #050054;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .campaign-services .campaign-service-box.featured {
    flex: 1;
    margin: 0;
  }
}
.campaign-services .campaign-service-box::before {
  content: "";
  display: block;
  flex: 0 0 auto;
  width: 70px;
  height: 70px;
  margin: 0 auto 20px;
  background: inherit;
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  line-height: 70px;
  background-image: url("../app/img/bubble.png");
  background-size: contain;
}
.campaign-services .campaign-service-box.card-1 {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.campaign-services .campaign-service-box.card-1::before {
  content: "2";
}
.campaign-services .campaign-service-box.card-3 {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.campaign-services .campaign-service-box.card-3::before {
  content: "3";
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .campaign-services .campaign-service-box {
    border-radius: 10px;
  }
}

section.mobile {
  margin-top: -150px;
}
section.mobile .campaign-services .campaign-service-box {
  background: #005d5f;
}
section.mobile .campaign-services .campaign-service-box.featured {
  background: #d2d5da !important;
}

.products {
  text-align: center;
}
.products h1, .products p {
  color: #fff;
}
.products .products-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 100px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .products .products-container {
    grid-template-columns: 1fr;
    margin-top: 50px;
  }
}
.products .products-container article {
  border: 1px solid #ccc;
  margin: 10px;
  border-radius: 10px;
  border-color: #1dffff;
  padding-bottom: 60px;
  color: #fff;
}
.products .products-container article img {
  margin-top: -80px;
}
.products .products-container article .product-title {
  font-size: 18px;
  font-weight: 700;
}
.products .products-container article .product-description {
  font-size: 14px;
  font-weight: 200;
}
.products .products-container article .price-heading {
  font-size: 14px;
  font-weight: 700;
  margin-top: 20px;
}
.products .products-container article .price {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ffda00;
}
.products .products-container article button {
  margin-bottom: -80px;
}
@media only screen and (min-width: 20em) and (max-width: 48em) {
  .products .products-container article:nth-child(1) {
    margin-bottom: 80px;
  }
}

